import { computed, onMounted, reactive } from "vue";
import { HtsReportService } from '@/apps/HTS/services/hts_report_service';
import { isEmpty } from "lodash";
import { loadingController } from "@ionic/vue";
import { toastWarning } from "@/utils/Alerts";
import { DateSelection } from "@/utils/PromptDateSelection"
import { DrillPatients } from "@/utils/PatientDrilldown"
import { Service } from "@/services/service";
import { appendCsvSignature, toCsv } from "@/utils/Export";
import dayjs from "dayjs";

const data = reactive({
    startDate: '',
    endDate: '',
    indicators: {} as any,
    period: '',
    dateGenerated: '',
    healthFacility: Service.getLocationName(),
    compiledBy: Service.getUserName()
})

const indicators = computed(() => {
    return Object.keys(data.indicators).reduce((indicators: any, key: string) => {
        indicators[key] = Array.isArray(data.indicators[key]) ? data.indicators[key].length : 0
        return indicators
    }, {})
})

export function SummaryController() {
    const generate = async (startDate ='', endDate = '', period='') => {
        (await loadingController.create({ message: "Please wait .."})).present();
        const report = new HtsReportService('HTS HIV TESTING SUMMARY')
        report.startDate = startDate || data.startDate
        report.endDate = endDate || data.endDate
        data.period = period || data.period
        try {
            data.indicators = await report.requestReport()
            data.dateGenerated = dayjs(Service.getSessionDate()).format('DD/MMM/YYYY HH:mm:ss')
        } catch (e) {
            toastWarning('Unable to generate report!')
        }
        loadingController.dismiss()
    }

    onMounted(() => isEmpty(data.indicators) && configure())

    const drill = (indicator: string) => {
        if (data.indicators[indicator]) DrillPatients(
            indicator.split('_').join(' '), data.indicators[indicator]
        )
        console.log(indicator)
    }

    const toPDF = () => {
        const printW = open('', '', 'width:1024px, height:768px')
        const content = document.getElementById("report-content")
        if (content && printW) {
            printW.document.write(`
                <html>
                <head>
                    <link rel="stylesheet" media="print"  href="/assets/css/hts-report-style-print.css"/>
                </head>
                <body>
                    ${content.innerHTML}
                </body>
                </html>
            `)
            setTimeout(() => { printW.print(); printW.close() }, 3500)
        }
    }

    const toCSV = () => {
        const columns = [['Indicator', 'value']]
        const rows = Object.keys(indicators.value).map(k => {
            return [k.split('_').join(' '), indicators.value[k]]
        })
        toCsv([columns], appendCsvSignature(rows, data),
            `Clinic HTS Weekly Summary ${data.healthFacility} ${data.period}`)
    }

    const configure = () => DateSelection({
        title: "HTS Weekly Summary",
        onFinish: (startDate: string, endDate: string, period: string) => {
            data.period = period
            data.startDate = startDate
            data.endDate = endDate
            generate()
        }
    })

    return {
        drill,
        toPDF,
        toCSV,
        generate,
        configure,
        indicators,
        data
    }
}